import './style.css'
import * as THREE from 'three'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import * as dat from 'dat.gui'
import {Text} from 'troika-three-text'

//letn
let clock, camera, scene, renderer;
let check = false;

// constants
const gui = new dat.GUI()
const loadingManager = new THREE.LoadingManager();
const loader = new GLTFLoader(loadingManager)
const canvas = document.querySelector('canvas.webgl')
const mouse = new THREE.Vector2();
const target = new THREE.Vector2();
const windowHalf = new THREE.Vector2( window.innerWidth / 2, window.innerHeight / 2 );
const windowRelMouse = new THREE.Vector2( window.innerWidth / 2, window.innerHeight / 2 );

const progressBar = document.getElementById('progress-bar')
const brief = document.getElementById('brief')
const sub = document.getElementById('sub')

//Loading
loadingManager.onStart = function() {
    brief.style.opacity = 0;
    sub.textContent = "Loading...";
}

loadingManager.onProgress = function(url, loaded, total) {
    progressBar.value = (loaded / total) * 100; 
}

loadingManager.onLoad = function() {
    brief.style.opacity = 1;
    sub.textContent = "McLaren Racing 3D Vitae";
}

//tick
init()
animate()

//init
function init() {

    //scene
    scene = new THREE.Scene();
    
    //camera
    camera = new THREE.PerspectiveCamera( 45, window.innerWidth / window.innerHeight, 0.1, 500 )
    camera.position.x = -0.5
    camera.position.y = 1
    camera.rotation.z = 0
    scene.add(camera)

    //Fog
    const near = 0;
    const far = 29;
    const color = '#011e3b';
    scene.fog = new THREE.Fog(color, near, far);
    scene.background = new THREE.Color(color);
    
    //About
    const textwall = new Text()
    scene.add(textwall)
    textwall.text = 'About'

    textwall.fontSize = 0.7

    textwall.position.x = 4
    textwall.position.y = 2
    textwall.position.z = -13

    textwall.rotation.x = 0
    textwall.rotation.y = 5
    textwall.rotation.z = 0

    textwall.color = 0x7dc7ff
    textwall.sync()

        //Personal Statement
        const persstate = new Text()
        scene.add(persstate)
        persstate.text = 'Personal Statement'
    
        persstate.fontSize = 0.7
    
        persstate.position.x = -10
        persstate.position.y = 2.9
        persstate.position.z = 36
    
        persstate.rotation.x = 0
        persstate.rotation.y = 0.4
        persstate.rotation.z = 0
    
        persstate.color = 0x7dc7ff
        persstate.sync()

    //Text Wall Sub
    const textwallsub = new Text()
    scene.add(textwallsub)
    textwallsub.text = "- 15 Years Old \n - McLaren F1 Fan \n - From The UK"
    
    textwallsub.fontSize = 0.5
    
    textwallsub.position.x = 4
    textwallsub.position.y = 1.2
    textwallsub.position.z = -13

    textwallsub.rotation.x = 0
    textwallsub.rotation.y = 5
    textwallsub.rotation.z = 0
    
    textwallsub.color = 0xffffff
    textwallsub.sync()

        //Personal Statement Sub
        const persstatesub = new Text()
        scene.add(persstatesub)
        persstatesub.text = "I am passionate about motorsport, and a fan of Mclaren across \n it's many motorsport entries. Watching and participating in \n motorsport, has interested me in gaining experience with \n a motorsport career. I believe my developing IT skills, in \n front-end, back-end and networking, are well suited \n to the needs of future motorsport technologies."
        
        persstatesub.fontSize = 0.3
        
        persstatesub.position.x = -10
        persstatesub.position.y = 2
        persstatesub.position.z = 36
            
        persstatesub.rotation.x = 0
        persstatesub.rotation.y = 0.4
        persstatesub.rotation.z = 0
        
        persstatesub.color = 0xffffff
        persstatesub.sync()

    function moveCamera() {
        const t = document.body.getBoundingClientRect().top; 
        const x = camera.rotation.x;    
        camera.position.z = t * -0.015;
        camera.rotation.x = x * 2;
        }
          
        document.body.onscroll = moveCamera;
        moveCamera();

    //clock
    clock = new THREE.Clock();
    
    //listeners
    document.addEventListener( 'mousemove', onMouseMove, false );
    window.addEventListener( 'resize', onResize, false );

    //renderer
    renderer = new THREE.WebGLRenderer( { antialias: true, canvas: canvas, depth: true} );
    renderer.setSize( window.innerWidth, window.innerHeight );
    document.body.appendChild( renderer.domElement );

  //Mobile Or PC
  var isMobile = {
    Android: function() {
        return navigator.userAgent.match(/Android/i);
    },
    BlackBerry: function() {
        return navigator.userAgent.match(/BlackBerry/i);
    },
    iOS: function() {
        return navigator.userAgent.match(/iPhone|iPad|iPod/i);
    },
    Opera: function() {
        return navigator.userAgent.match(/Opera Mini/i);
    },
    Windows: function() {
        return navigator.userAgent.match(/IEMobile/i) || navigator.userAgent.match(/WPDesktop/i);
    },
    any: function() {
        return (isMobile.Android() || isMobile.BlackBerry() || isMobile.iOS() || isMobile.Opera() || isMobile.Windows());
    }
  };

  //PC
  if(!isMobile.any()) {
    console.log("detected pc/tablet browser => quality increased");
    loader.load('./pc/pc.gltf', (gltf) => {
        gltf.scene.scale.set(1, 1, 1);
        scene.add(gltf.scene);
    });
    }
    
    //Mobile
    if(isMobile.any()) {
          console.log("detected mobile browser => quality reduced");
          loader.load('./mobile/mobile.gltf', (gltf) => {
                gltf.scene.scale.set(0.5, 0.5, 0.5)
                scene.add(gltf.scene);
          });
  }

}

//mouse move
function onMouseMove( event ) {

	mouse.x = ( event.clientX - windowRelMouse.x );
	mouse.y = ( event.clientY - windowRelMouse.x );

}

//window resized
function onResize( event ) {

	const width = window.innerWidth;
	const height = window.innerHeight;
  
  windowHalf.set( width / 2, height / 2 );
	
  camera.aspect = width / height;
	camera.updateProjectionMatrix();
	renderer.setSize( width, height );
				
}

//animate
function animate() {
	target.x = ( 1 - mouse.x ) * 0.0005;
    target.y = ( 1 - mouse.y ) * 0.0005;
    camera.rotation.x += 1 * ( target.y - camera.rotation.x );
    camera.rotation.y += 1 * ( target.x - camera.rotation.y );
    requestAnimationFrame( animate );
    renderer.render( scene, camera );
}